input[type="number"] {
  position: relative;
  width: 100px;
  height: 100px;
  background: none;
  color: white;
  font-size: 2em;
  line-height: 2em;
  font-weight: 700;
  text-align: center;
  border: 0;
  outline: 0; }
  input[type="number"]::-webkit-textfield-decoration-container {
    display: inline-flex;
    cursor: pointer; }
    input[type="number"]::-webkit-textfield-decoration-container:before, input[type="number"]::-webkit-textfield-decoration-container:after {
      position: absolute;
      top: 37.5px;
      background: rgba(0, 0, 0, 0.2);
      width: 25px;
      height: 25px;
      line-height: 25px;
      text-indent: 0;
      text-align: center; }
    input[type="number"]::-webkit-textfield-decoration-container:before {
      content: '-';
      left: 0;
      line-height: 22px; }
    input[type="number"]::-webkit-textfield-decoration-container:after {
      content: '+';
      right: 0; }
  input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    -moz-appearance: textfield;
    transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -webkit-transform-origin: left top;
    transform-origin: left top;
    cursor: pointer;
    width: 25px;
    top: 37.5px;
    left: 43.75px;
    z-index: 1; }

